	module.exports = {"data":{"markdownRemark":{"html":"<p>Apart from <a href=\"/force-y\">Force.Y</a> , I divided the game software development room ( <a href=\"/tag/evil-kingdom\">MMORPG</a> development room site) into separate sites and managed it named \"Studio MAKINA\". Here I was doing various Web-based experimental things. At this time I started to use XML + XSL structure which I had been adopting entirely from early to early summer 2011 even at danmaq, such as creating CGI with C ++. (By the way, \" <a href=\"/makina-city\">MAKINA - CITY</a> \" prior to the transition to <a href=\"/force-y\">danmaq</a> used <a href=\"/force-y\">Force.Y</a> plain HTML of the yuzuri)</p>","frontmatter":{"cover":null,"date":"2004-05-27T00:00:00+09:00","strDate":"2004/5/26","title":"Studio MAKINA"}}},"pathContext":{"path":"/en/blog/2004/05/27-studio-makina/","langKey":"en"}}

